* {
  font-family: "Source Sans Pro", sans-serif;
}

html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: inherit;
}

ul,
ol,
li {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
  font-weight: inherit;
  font-size: inherit;
}

img {
  max-width: 100%;
  display: block;
}

input,
button,
textarea,
select {
  background-color: inherit;
  font-size: inherit;
  font-weight: inherit;
  color: inherit;
}

a {
  text-decoration: none;
}

html,
body {
  height: 100%;
}

* {
  font-family: "Source Sans Pro", sans-serif;
}

body {
  margin: 0;
  background-color: #f3f3f3;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

.content {
  flex: 1 0 auto;
}

.main {
  flex: 1 0 auto;
}

.footer {
  flex: 0 0 auto;
}

img {
  max-width: 100%;
}

.container {
  max-width: 1100px;
  margin: 0 auto;
  padding: 0 15px;
  height: 100%;
}

.header {
  background-color: #fff;
  position: fixed;
  width: 100%;
  border-bottom: 1px solid #e0dfdc;
}
.header__logo-item {
  display: flex;
  width: auto;
}
.header__logo {
  color: #0A66C2;
  font-size: 25px;
  font-weight: 800;
}
.header__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: row;
  height: 53px;
  padding: 0 10px 0 10px;
}

.signupin {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin-top: 200px;
}

.form {
  display: flex;
  justify-content: center;
}
.form__inner {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: #fff;
  width: 400px;
  height: 100%;
  padding: 25px;
  border: 1px solid #e0dfdc;
  border-radius: 7px;
}
.form__top {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding-bottom: 20px;
  padding-left: 10px;
}
.form__title {
  color: #404448;
  font-size: 21px;
  font-weight: 600;
  padding-bottom: 5px;
}
.form__subtitle-item {
  display: flex;
  align-items: center;
  color: #404448;
}
.form__place {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-bottom: 15px;
}
.form__place-input {
  width: 100%;
  height: 40px;
  border: 1px solid #e0dfdc;
  border-radius: 7px;
  padding-left: 25px;
  font-size: 15px;
}
.form__place-input:hover {
  background-color: #f3f3f3;
}
.form__place-input:focus {
  outline: none;
}
.form__place-input + .form__place-input {
  margin-top: 10px;
}
.form__item-btn {
  display: flex;
  margin: 10px 0 20px 0;
}
.form__item-submit {
  display: block;
  height: fit-content;
  width: 100%;
  color: #fff;
  background-color: #2078cf;
  font-size: 17px;
  font-weight: 600;
  padding: 7px;
  letter-spacing: 0.01em;
  border-radius: 7px;
  cursor: pointer;
}
.form__item-submit:hover {
  background-color: #0A66C2;
  transition: all 200ms ease-in-out;
}
.form__down {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}
.form__down-item {
  display: flex;
}
.form__down-question {
  display: flex;
  align-items: center;
  color: #404448;
  padding-right: 12px;
}
.form__link {
  display: flex;
}
.form__link-redirect {
  display: block;
  height: fit-content;
  width: fit-content;
  color: #ffffff;
  background-color: #3c9458;
  font-size: 17px;
  font-weight: 600;
  padding: 7px 15px;
  letter-spacing: 0.01em;
  border-radius: 7px;
  cursor: pointer;
}
.form__link-redirect:hover {
  background-color: #2f7244;
  transition: all 200ms ease-in-out;
}

.msg {
  display: flex;
  justify-content: center;
  align-items: center;
  height: fit-content;
  max-width: 350px;
}
.msg__item-red {
  max-width: 95%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #bd2020;
}
.msg__item-green {
  max-width: 95%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #2f7244;
}

.allcontent {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
  height: auto;
  padding-top: 77px;
}

.content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-left: 20px;
  max-width: 810px;
  height: auto;
}

.share {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: row;
  background-color: #fff;
  border: 1px solid #e0dfdc;
  border-radius: 7px;
  height: 120px;
  padding: 20px;
}
.share__image {
  display: flex;
  border-radius: 50%;
  justify-content: center;
  object-fit: cover;
  width: 60px;
  height: 60px;
  border: 1px solid #e0dfdc;
}
.share__form {
  display: flex;
  height: 70px;
  width: 570px;
  margin: 20px;
  border: 1px solid #e0dfdc;
  border-radius: 7px;
  font-size: 18px;
}
.share__form:hover {
  background-color: #f3f3f3;
}
.share__share-text {
  display: flex;
}
.share__form-textarea {
  display: flex;
  width: 100%;
  font-size: 14px;
  padding: 10px 10px;
  line-height: 20px;
  resize: none;
}
.share__form-textarea:focus {
  outline: none !important;
  border: 1px solid #8ab0d6;
  border-radius: 7px;
}
.share__button {
  display: block;
  height: fit-content;
  width: 20%;
  color: #fff;
  background-color: #2078cf;
  padding: 5px;
  font-size: 18px;
  font-weight: 600;
  letter-spacing: 0.01em;
  border-radius: 7px;
  cursor: pointer;
}
.share__button:hover {
  background-color: #0A66C2;
  transition: all 200ms ease-in-out;
}

.border {
  display: flex;
  justify-content: center;
  margin: 20px 0 20px 0;
}
.border__item {
  width: 99%;
  height: 1px;
  background-color: #bfbebb;
}

.post {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: #fff;
  border: 1px solid #e0dfdc;
  border-radius: 7px;
  height: auto;
  padding: 20px 20px 0 20px;
}
.post__header {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 5px;
}
.post__header-image {
  display: flex;
  border-radius: 50%;
  justify-content: center;
  object-fit: cover;
  width: 60px;
  height: 60px;
  border: 1px solid #e0dfdc;
}
.post__header-title {
  display: flex;
  flex-direction: column;
  padding-left: 18px;
}
.post__header-name {
  display: flex;
  color: #404448;
  font-size: 20px;
  font-weight: 600;
  max-width: 200px;
  padding-bottom: 3px;
}
.post__header-time {
  display: flex;
  color: #404448;
  font-size: 14px;
  font-weight: 500;
}
.post__text {
  display: flex;
  color: black;
  font-size: 14px;
  font-weight: 400;
  margin-top: 10px;
  font-family: "Open Sans", sans-serif;
}
.post__buttons {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  margin-top: 15px;
}
.post__btn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: fit-content;
  height: fit-content;
  padding: 0 7px 0 0;
  color: #707070;
  border-radius: 7px;
  font-size: 15px;
  letter-spacing: 0.01em;
  cursor: pointer;
}
.post__btn:hover {
  transition: all 200ms ease-in-out;
  background-color: #e0dfdc;
  color: #191919;
}

.profile {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: auto;
}

.setimage {
  display: flex;
  flex-direction: column;
  background-color: #fff;
  border: 1px solid #e0dfdc;
  border-radius: 7px;
  height: auto;
  padding: 20px;
  width: 500px;
}
.setimage__title {
  display: flex;
  margin-left: 10px;
  color: #404448;
  font-size: 20px;
  font-weight: 600;
}
.setimage__img {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.setimage__image {
  display: flex;
  justify-content: center;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 10px;
  width: 120px;
  height: 120px;
  border: 1px solid #e0dfdc;
}
.setimage__item {
  position: relative;
  padding: 10px 0 0 10px;
}
.setimage__upload-input {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  font-size: 0;
  cursor: pointer;
}
.setimage__button {
  display: inline-flex;
  max-height: fit-content;
  width: fit-content;
  color: #ffffff;
  background-color: #3c9458;
  font-size: 14px;
  font-weight: 600;
  padding: 4px 8px;
  letter-spacing: 0.01em;
  border-radius: 7px;
}
.setimage__preview {
  display: inline-block;
  min-width: 100px;
  padding: 10px 0 0 0;
}
.setimage__preview img {
  max-width: 100%;
}
.setimage__save-button {
  display: block;
  height: fit-content;
  width: fit-content;
  color: #fff;
  background-color: #2078cf;
  padding: 7px;
  font-weight: 600;
  letter-spacing: 0.01em;
  border-radius: 7px;
  cursor: pointer;
  margin: 5px 0 0 10px;
  font-size: 15px;
}
.setimage__save-button:hover {
  background-color: #0A66C2;
  transition: all 200ms ease-in-out;
}

.brd {
  display: flex;
  justify-content: center;
  margin: 20px 0 20px 0;
}
.brd__item {
  width: 495px;
  height: 1px;
  background-color: #bfbebb;
  position: absolute;
}

.about {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  flex-direction: column;
  background-color: #fff;
  border: 1px solid #e0dfdc;
  border-radius: 7px;
  height: auto;
  padding: 20px;
  width: 500px;
}
.about__title {
  display: flex;
  color: #404448;
  margin-left: 10px;
  font-size: 20px;
  font-weight: 600;
  padding-bottom: 10px;
}
.about__form {
  display: flex;
  flex-direction: row;
  height: 100%;
  width: 450px;
  border: 1px solid #e0dfdc;
  border-radius: 7px;
  margin-left: 5px;
  font-size: 18px;
  font-family: serif;
}
.about__form:hover {
  background-color: #f3f3f3;
}
.about__form-textarea {
  display: flex;
  width: 100%;
  min-height: 120px;
  font-size: 14px;
  padding: 10px 10px;
  line-height: 20px;
  resize: none;
}
.about__form-textarea:focus {
  outline: none !important;
  border: 1px solid #8ab0d6;
  border-radius: 7px;
}
.about__button {
  display: block;
  height: fit-content;
  width: fit-content;
  color: #fff;
  background-color: #2078cf;
  padding: 7px;
  font-weight: 600;
  letter-spacing: 0.01em;
  border-radius: 7px;
  cursor: pointer;
  margin: 15px 0 0 10px;
  font-size: 15px;
}
.about__button:hover {
  background-color: #0A66C2;
  transition: all 200ms ease-in-out;
}

.friendbox {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: auto;
}

.friends {
  display: flex;
  flex-direction: row;
  align-items: center;
  background-color: #fff;
  border: 1px solid #e0dfdc;
  border-radius: 7px;
  height: 200px;
  padding: 20px;
  width: 500px;
}
.friends__img {
  display: flex;
}
.friends__image {
  display: flex;
  justify-content: center;
  margin-left: 10px;
  border-radius: 50%;
  object-fit: cover;
  width: 120px;
  height: 120px;
  border: 1px solid #e0dfdc;
}
.friends__namebtn {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  margin-left: 20px;
}
.friends__name-item {
  display: flex;
  align-items: center;
  margin-top: 20px;
  height: 40px;
}
.friends__name {
  display: flex;
  color: #000;
  font-size: 20px;
  font-weight: 600;
}
.friends__name:hover {
  border-bottom: 1px solid #000;
  transition: all 200ms ease-in-out;
}
.friends__name__button-item {
  display: flex;
}
.friends__button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: fit-content;
  height: fit-content;
  padding: 0 7px 0 0;
  color: #707070;
  border-radius: 7px;
  font-size: 15px;
  letter-spacing: 0.01em;
  cursor: pointer;
  font-size: 13px;
  font-weight: 600;
}
.friends__button:hover {
  transition: all 200ms ease-in-out;
  background-color: #e0dfdc;
  color: #191919;
}

.nav {
  display: flex;
  justify-content: flex-end;
  width: 100%;
  height: 100%;
}
.nav__list {
  display: flex;
  align-items: center;
  justify-content: center;
}
.nav__list-item {
  display: flex;
  align-items: center;
  height: 100%;
}
.nav__list-item + .nav__list-item {
  margin-left: 10px;
}
.nav__list-item + .nav__list-item + .nav__list-item {
  margin-left: 15px;
  position: relative;
}
.nav__list-item + .nav__list-item + .nav__list-item::before {
  content: "";
  margin-right: 10px;
  width: 1px;
  height: 40%;
  background-color: #bfbebb;
}
.nav__list-link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: fit-content;
  height: fit-content;
  color: #707070;
  font-size: 18px;
  letter-spacing: 0.01em;
  cursor: pointer;
  margin-left: 5px;
  font-weight: 600;
}
.nav__list-link:hover {
  transition: all 200ms ease-in-out;
  color: #191919;
}

.aside {
  display: flex;
}
.aside__items {
  display: flex;
  background-color: #fff;
  width: 230px;
  border: 1px solid #e0dfdc;
  border-radius: 7px;
  height: auto;
  padding: 20px 20px 0 20px;
  display: flex;
  align-items: center;
  flex-direction: column;
  position: fixed;
}
.aside__signature {
  display: flex;
  align-items: center;
  flex-direction: column;
  padding-bottom: 15px;
  height: auto;
  position: relative;
}
.aside__signature::after {
  content: "";
  position: absolute;
  bottom: 0;
  width: 230px;
  height: 1px;
  background-color: #e0dfdc;
}
.aside__item-image {
  justify-content: center;
  align-items: center;
  display: flex;
}
.aside__item-img {
  justify-content: center;
  align-items: center;
  display: flex;
  object-fit: cover;
  border-radius: 50%;
  width: 100px;
  height: 100px;
  border: 1px solid #e0dfdc;
}
.aside__item-name {
  color: #404448;
  font-size: 20px;
  font-weight: 600;
  max-width: 200px;
  padding: 7px 0 7px 0;
}
.aside__item-text {
  max-width: 200px;
  color: #404448;
  font-size: 14px;
  text-align: center;
}
.aside__subscribe-item {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
}
.aside__menu-list {
  display: flex;
  margin: 10px 0 10px 0;
  flex-direction: column;
}
.aside__menu-link {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  height: 45px;
  width: 230px;
  color: #707070;
  font-size: 17px;
  font-weight: 600;
  padding-left: 25px;
  cursor: pointer;
}
.aside__menu-link:hover {
  transition: all 200ms ease-in-out;
  color: #191919;
  background-color: #e0dfdc;
}

.error {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin-top: 300px;
  font-size: 30px;
  color: #404448;
}